.advantages {
  margin-top: 100px;
}

.advantages__title {
  font-weight: bold;
  font-size: 50px;
  font-family: serif;
}

.advantages-wrapper {
  display: flex;
  align-items: center;
  margin-top: 50px;
}

.advantages__list {
  @include reset-list;

  margin-left: 100px;
}

.advantages__list-item {
  margin-bottom: 30px;
  padding-left: 90px;

  font-size: 16px;

  background-repeat: no-repeat;
  background-position: left top;
}

.advantages__list-item--quarantee {
  background-image: url("../img/icon-1.png");
}

.advantages__list-item--fees {
  background-image: url("../img/icon-2.png");
}

.advantages__list-item--escort {
  background-image: url("../img/icon-3.png");
}

.advantages__btn {
  margin-left: 100px;
}

.advantages__list-item-header {
  font-weight: bold;
  font-size: 16px;
}

@media screen and (max-width: 991px) {
  .advantages-wrapper {
    flex-direction: column;

    & img {
      width: 100%;
    }
  }

  .advantages__list {
    margin-top: 50px;
    margin-left: 0;
  }
}

@media screen and (max-width: 500px) {
  .advantages__title {
    font-size: 40px;
  }
}

@media screen and (max-width: 400px) {
  .advantages__title {
    font-size: 30px;
  }

  .advantages__list {
    margin-top: 30px;
  }

  .advantages__list-item {
    margin-right: 20px;
    margin-left: 20px;
    padding-left: 50px;

    font-size: 14px;

    background-size: 30px auto;
  }

  .advantages__btn {
    margin: 0 auto;
  }
}
