.modal-input {
  border: 1px solid $basic-light-grey;
  border-radius: 5px;
  padding: 5px 10px;
  display: block;
  margin: 0 auto;

  &:hover {
    opacity: 0.7;
  }

  &:focus {
    outline: none;
    opacity: 0.7;
  }
}

.modal-btn {
  background-color: $basic-red;
  border: none;
  border-radius: 5px;
  padding: 5px 15px;
  color: $basic-white;

  &:hover {
    opacity: 0.7;
  }

  &:focus {
    outline: none;
    opacity: 0.7;
  }
}

.close {
  background-image: url("../img/close.png");
  background-repeat: no-repeat;
  background-position: center;
  width: 18px;
  height: 18px;
  border: none;
  background-color: transparent;

  &:hover {
    opacity: 0.7;
  }

  &:focus {
    outline: none;
    opacity: 0.7;
  }
}
