.testemonials {
  margin-top: 100px;
}

.testemonials__title {
  font-weight: bold;
  font-size: 50px;
  font-family: serif;
}

.testemonials__list {
  @include reset-list;

  position: relative;

  margin-top: 50px;
}

.testemonials__list-item {
  padding-left: 100px;

  background-image: url("../img/icon.png");
  background-repeat: no-repeat;
  background-position: left top;
}

.testemonials__text {
  width: 90%;

  font-size: 18px;
}

.testemonials__author {
  font-size: 14px;

  & span {
    font-weight: bold;
  }
}

@media screen and (max-width: 500px) {
  .testemonials__title {
    font-size: 40px;
  }

  .testemonials__text {
    font-size: 16px;
  }
}

@media screen and (max-width: 400px) {
  .testemonials__title {
    font-size: 30px;
  }

  .testemonials__list-item {
    padding-left: 50px;

    background-position: left top;
    background-size: 30px auto;
  }
}
