.header {
  margin-top: 30px;
}

.page-header__logo {
  margin-right: 50px;
}

.navbar-toggler-icon {
  background-image: url("../img/menu.webp");
  background-repeat: no-repeat;

  &:focus {
    outline: none;
  }
}

.main-nav__list {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;
}

.user-nav {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;
}

.input-search {
  width: 150px;

  background-image: url("../img/icon-searh.png");
  background-repeat: no-repeat;
  background-position: right center;
  border: none;
  border-bottom: 1px solid $basic-light-grey;

  &:hover {
    opacity: 0.7;
  }

  &:focus {
    outline: none;
    opacity: 0.7;
  }
}

.user-nav__link--sign-in {
  color: $basic-grey;
  text-decoration: none;

  &:hover {
    color: $basic-red;
  }

  &:focus {
    color: $basic-red;

    outline: none;
  }
}

.user-nav__link--sign-up {
  padding: 10px 20px;

  color: $basic-white;
  text-decoration: none;

  background-color: $basic-red;
  border-radius: 5px;

  &:hover {
    color: $basic-grey;

    background-color: $basic-light-grey;
  }

  &:focus {
    color: $basic-grey;

    background-color: $basic-light-grey;
    outline: none;
  }
}

.navbar-collapse {
  display: flex;
  justify-content: space-between;
  width: auto;
}

.user-nav__item {
  margin-right: 30px;

  font-size: 14px;

  &:last-child {
    margin-right: 0;
  }
}

.site-list__link {
  margin-right: 30px;

  font-size: 14px;
  color: $basic-grey;
  text-decoration: none;

  &:hover {
    color: $basic-red;
  }

  &:focus {
    color: $basic-red;

    outline: none;
  }
}

.navbar-expand-lg {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-width: 991px) {
  .main-nav__list {
    width: 100%;
  }

  .user-nav {
    width: 100%;
    margin-top: 40px;
  }

  .navbar-collapse {
    display: flex;
    flex-direction: column;
    margin-top: 30px;
  }

  .user-nav__item {
    margin-bottom: 30px;
      font-size: 16px;
  }

  .input-search {
    display: none;
  }

  .site-list__link {
    font-size: 16px;
  }

  .site-list__item {
    margin-bottom: 20px;
  }
}
