.main-screen {
  margin-top: 100px;
  margin-left: 0;
  padding-bottom: 250px;

  background-image: url("../img/main-img.jpg");
  background-repeat: no-repeat;
  background-position: right bottom;
}

.main-screen__title {
  margin-left: 0;

  font-weight: bold;
  font-size: 100px;
  line-height: 80px;
  font-family: serif;

  & span {
    color: $basic-red;
  }
}

.main-screen__subtitle {
  width: 30%;
  margin-top: 40px;

  font-size: 16px;
  line-height: 26px;
}

.main-button {
  display: block;
  width: 230px;
  margin-top: 50px;
  padding: 15px 50px;

  font-size: 14px;
  text-align: center;
  color: $basic-white;
  text-decoration: none;

  background-color: $basic-red;
  border-radius: 5px;

  &:hover {
    color: $basic-grey;

    background-color: $basic-light-grey;
  }

  &:focus {
    color: $basic-grey;

    background-color: $basic-light-grey;
    outline: none;
  }
}

@media screen and (max-width: 991px) {
  .main-screen {
    padding-bottom: 530px;
  }

  .main-screen__subtitle {
    width: 100%;
  }

  .main-button {
    margin-top: 30px;
  }
}

@media screen and (max-width: 767px) {
  .main-screen__title {
    font-size: 80px;
  }

  .main-screen__subtitle {
    margin-top: 20px;
  }

  .main-button {
    margin-top: 30px;
  }
}

@media screen and (max-width: 500px) {
  .main-screen__title {
    font-size: 60px;
    line-height: 60px;
  }

  .main-screen__subtitle {
    margin-top: 20px;
  }

  .main-button {
    margin-top: 30px;
  }
}

@media screen and (max-width: 400px) {
  .main-screen__title {
    font-size: 50px;
    line-height: 50px;
  }
}
