.gallery {
  margin-top: 100px;
}

.gallery__title {
  margin-left: 30%;

  font-weight: bold;
  font-size: 50px;
  font-family: serif;
}

.gallery__list {
  @include reset-list;

  position: relative;

  display: flex;
  width: 100%;
  margin-top: 50px;
}

.gallery__list-item img {
  width: 24%;
  margin: 0;
  margin-right: 8px;
}

.prev-icon {
  position: absolute;
  top: 50%;
  left: 20px;

  width: 20px;
  height: 20px;

  background-image: url("../img/arrow-left.png");
  background-repeat: no-repeat;
}

.next-icon {
  position: absolute;
  top: 50%;
  right: 20px;

  width: 20px;
  height: 20px;

  background-image: url("../img/arrow-right.png");
  background-repeat: no-repeat;
}

@media screen and (max-width: 991px) {
  .gallery__title {
    margin-left: 0;
  }

  .gallery__list-item img {
    width: 48%;
    margin-right: 11px;
  }
}

@media screen and (max-width: 767px) {
  .gallery__list-item img {
    width: 47%;
    margin-bottom: 20px;
  }
}

@media screen and (max-width: 500px) {
  .gallery__title {
    font-size: 40px;
  }
}

@media screen and (max-width: 400px) {
  .gallery__title {
    margin-bottom: 0;

    font-size: 30px;
  }

  .gallery__list-item img {
    width: 100%;
  }
}
