.page-footer {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 100px;
  padding-top: 30px;
  padding-bottom: 30px;

  border-top: 1px solid $basic-light-grey;
}

.footer {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;
}

.footer__item {
  margin-right: 30px;
}

.footer__link {
  font-size: 14px;
  color: $basic-grey;
  text-decoration: none;

  &:hover {
    color: $basic-light-grey;
  }

  &:focus {
    color: $basic-light-grey;

    outline: none;
  }
}


@media screen and (max-width: 500px) {
  .footer {
    flex-direction: column;
    width: auto;
  }

  .footer__item {
    width: 50%;
    margin-right: 0;
    margin-bottom: 20px;
  }

  .page-footer__logo {
    margin: 0 auto;
  }
}
