.partners {
  margin-top: 100px;
}

.partners__title {
  font-weight: bold;
  font-size: 50px;
  font-family: serif;
}

.partners__list {
  @include reset-list;

  display: grid;
  margin-top: 30px;

  grid-gap: 20px;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}


.partners__list-item {
  display: inline-block;
  margin: auto;
}

@media screen and (max-width: 991px) {
  .partners__list {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media screen and (max-width: 767px) {
  .partners__list {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 500px) {
  .partners__title {
    font-size: 40px;
  }

  .partners__list {
    margin-top: 20px;
  }
}

@media screen and (max-width: 400px) {
  .partners__title {
    margin-bottom: 30px;

    font-size: 30px;
  }

  .partners__list {
    grid-template-columns: 1fr;
  }
}
