.realize {
  margin-top: 100px;
  padding: 220px 0;

  background-image: url("../img/img.webp");
  background-repeat: no-repeat;
  background-position: left center;
}

.realize__title {
  margin-left: 500px;

  font-weight: bold;
  font-size: 50px;
  font-family: serif;
}

.realize__btn {
  display: block;
  margin-left: 500px;
}

@media screen and (max-width: 991px) {
  .realize {
    padding-top: 0;
    padding-bottom: 500px;

    background-position: top right;
  }

  .realize__title {
    margin-left: 0;
  }

  .realize__btn {
    margin-left: 0;
  }
}

@media screen and (max-width: 500px) {
  .realize {
    padding-bottom: 300px;

    background-position: bottom center;
    background-size: 300px auto;
  }

  .realize__title {
    font-size: 40px;
  }

  .testemonials__text {
    font-size: 16px;
  }
}

@media screen and (max-width: 400px) {
  .realize__title {
    font-size: 30px;
  }
}
